.skills-icon {
	font-size: 6.4rem;
	color: #0077cc;
	transition: transform 5s ease-in-out;
}

@keyframes pulse {
	0% {
		-webkit-transform: scale(0.2);
		opacity: 0.2;
	}
	100% {
		-webkit-transform: scale(1);
		opacity: 1;
	}
}
.skills-icon {
	animation-duration: 1.5s;
	animation-name: pulse;
	animation-delay: 2s;
	animation-iteration-count: 1;
	animation-fill-mode: forwards;
}
