.margin-bottom {
	margin-top: 6rem;
	height: 50%;
}

.contact-card {
	display: flex;
	display: flex;
	flex-direction: column;
	align-items: center;
	align-content: center;
	justify-content: center;
	padding: 3.6rem;
	background-color: #fff;
	border-radius: 1rem;
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
	margin: 0 auto;
	text-align: center;
	width: 75%;
	border: 1px inset #ff6b6b;
}

.contact-card h2 {
	font-size: 24px;
}

.contact-card p {
	font-size: 16px;
	color: #666;
	margin-bottom: 20px;
}

.contact-info {
	display: flex;
	justify-content: space-around;
	font-size: 1.6rem;
	align-items: center;
}

.contact-info p,
.contact-info a {
	margin: 0;
	text-decoration: none;
	background-color: #ff6b6b;
	border-radius: 2rem;
	padding: 1.2rem;
	color: #fff;
}

/* Media query for responsiveness */
@media (max-width: 768px) {
	.contact-card {
		max-width: 100%;
	}
}
