.about-container {
	display: flex;
	flex-direction: column;
	gap: 2rem;
	align-items: center;
	justify-content: center;
	height: 100%;
	width: 100%;
	margin-bottom: 12rem;
}
.about-links{
  display: flex;
  gap: 1.1rem;
  align-items: center;
  margin-bottom: 2.4rem;
}

.about-links_link{
  font-size: 42rem;
  width: 2.4rem;
}

.about-content {
	display: flex;
	flex-direction: column;
	gap: 20px;
}
